import React from "react";
import {
  Box,
  Container,
  Typography,
  Paper,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Stack,
  Breadcrumbs,
  Link,
} from "@mui/material";

import { Link as RouterLink } from "react-router-dom";

import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import WarningAmberIcon from "@mui/icons-material/WarningAmber";
import InfoIcon from "@mui/icons-material/Info";
import GavelIcon from "@mui/icons-material/Gavel";
import SecurityIcon from "@mui/icons-material/Security";
import AccountBalanceIcon from "@mui/icons-material/AccountBalance";
import VerifiedUserIcon from "@mui/icons-material/VerifiedUser";

/* Brand Colors */
const DARK_BLUE = "#193968";
const GOLD = "#cc9840";
const PASTEL_GREY_BLUE = "#aeb9c7";
const GRAY_BLUE = "#5a7191";

const Disclaimer = () => {

  const disclaimerSections = [
    {
      icon: <InfoIcon sx={{ color: GOLD }} />,
      title: "General Information",
      content:
        "The information contained on BankMudraa website is for general information purposes only. BankMudraa shall not be held liable for any errors or omissions in the contents on the Service.",
    },
    {
      icon: <AccountBalanceIcon sx={{ color: GOLD }} />,
      title: "Banking Services",
      content:
        "BankMudraa provides banking infrastructure services including DMT, AEPS, BBPS, and PPI in compliance with RBI and NPCI regulations.",
    },
    {
      icon: <SecurityIcon sx={{ color: GOLD }} />,
      title: "Security & Data Protection",
      content:
        "We employ industry-standard security measures including encryption and SSL technology, however absolute security over internet transmission cannot be guaranteed.",
    },
    {
      icon: <GavelIcon sx={{ color: GOLD }} />,
      title: "Legal Compliance",
      content:
        "All transactions are governed by applicable Indian laws including the Payment and Settlement Systems Act and RBI guidelines.",
    },
    {
      icon: <VerifiedUserIcon sx={{ color: GOLD }} />,
      title: "Service Availability",
      content:
        "Services may be temporarily unavailable due to maintenance, network failures, or regulatory directives.",
    },
  ];

  const importantPoints = [
    "Transaction limits may change as per RBI guidelines",
    "Verify beneficiary details before transactions",
    "Failed transactions reversed within 7 to 15 working days (subject to bank timelines).",
    "Charges and commission may change",
    "Accounts may be suspended for suspicious activities",
    "Jurisdiction: Faridabad, Haryana, India",
    "Users must be 18 years or older",
    "BankMudraa not liable for third-party failures",
  ];

  return (
    <Box sx={{ bgcolor: "#f5f7fa" }}>

      {/* ===== STANDARD HERO ===== */}
      <Box
        sx={{
          background: "linear-gradient(135deg, rgb(23,59,75) 0%, #23627e 100%)",
          position: "relative",
          overflow: "hidden",
          "&::before": {
            content: '""',
            position: "absolute",
            inset: 0,
            backgroundImage:
              "radial-gradient(circle at 20% 50%, rgba(255,255,255,0.1) 0%, transparent 50%), radial-gradient(circle at 80% 80%, rgba(255,255,255,0.1) 0%, transparent 50%)"
          }
        }}
      >
        <Container maxWidth="lg" sx={{ py: { xs: 6, md: 10 }, position: "relative", zIndex: 1 }}>

          <Breadcrumbs
            separator={<NavigateNextIcon fontSize="small" sx={{ color: "rgba(255,255,255,0.7)" }} />}
            sx={{ mb: 3 }}
          >
            <Link
              component={RouterLink}
              to="/"
              sx={{
                color: "rgba(255,255,255,0.9)",
                textDecoration: "none",
                "&:hover": { color: "white", textDecoration: "underline" }
              }}
            >
              Home
            </Link>

            <Typography sx={{ color: "white", fontWeight: 500 }}>
              Disclaimer
            </Typography>
          </Breadcrumbs>

          <Typography
            variant="h2"
            sx={{
              color: "white",
              fontWeight: 700,
              mb: 2,
              fontSize: { xs: "2rem", md: "3rem" }
            }}
          >
            Disclaimer
          </Typography>

          <Typography
            variant="h6"
            sx={{
              color: "rgba(255,255,255,0.95)",
              maxWidth: "800px",
              lineHeight: 1.7
            }}
          >
            Please read this disclaimer carefully before using our services.
          </Typography>

        </Container>
      </Box>

      {/* LAST UPDATED */}
      {/* <Container maxWidth="lg" sx={{ py: 2 }}>
        <Typography textAlign="right" color={GRAY_BLUE} variant="body2">
          Last Updated: February 2026
        </Typography>
      </Container> */}

      {/* DISCLAIMER SECTIONS */}
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Stack spacing={3}>
          {disclaimerSections.map((section, index) => (
            <Paper key={index} sx={{ p: 4, borderRadius: 3 }}>
              <Stack direction="row" alignItems="center" spacing={2} mb={2}>
                {section.icon}

                <Typography variant="h5" fontWeight={700}>
                  {section.title}
                </Typography>
              </Stack>

              <Typography color={GRAY_BLUE} lineHeight={1.8}>
                {section.content}
              </Typography>
            </Paper>
          ))}
        </Stack>
      </Container>

      {/* IMPORTANT POINTS */}
      <Box sx={{ bgcolor: "white", py: 8 }}>
        <Container maxWidth="lg">
          <Paper sx={{ p: 4, borderRadius: 3 }}>
            <Stack direction="row" alignItems="center" spacing={2} mb={3}>
              <WarningAmberIcon sx={{ color: GOLD }} />

              <Typography variant="h5" fontWeight={700}>
                Important Points
              </Typography>
            </Stack>

            <Divider sx={{ mb: 3 }} />

            <List>
              {importantPoints.map((point, index) => (
                <ListItem key={index}>
                  <ListItemIcon>
                    <Box
                      sx={{
                        width: 24,
                        height: 24,
                        borderRadius: "50%",
                        bgcolor: DARK_BLUE,
                        color: "white",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        fontSize: 12,
                        fontWeight: 700,
                      }}
                    >
                      {index + 1}
                    </Box>
                  </ListItemIcon>

                  <ListItemText
                    primary={point}
                    primaryTypographyProps={{ color: GRAY_BLUE }}
                  />
                </ListItem>
              ))}
            </List>
          </Paper>
        </Container>
      </Box>

      {/* CONTACT SECTION */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Paper
          sx={{
            p: 4,
            borderRadius: 3,
            background: `linear-gradient(135deg, ${DARK_BLUE}, #0e2244)`,
            textAlign: "center",
            color: "white",
          }}
        >
          <Typography variant="h5" fontWeight={700} mb={2}>
            Questions About This Disclaimer?
          </Typography>

          <Typography color={PASTEL_GREY_BLUE} mb={3}>
            Contact our legal team for clarification.
          </Typography>

          <Typography color={GOLD}>
            Email: support@bankmudraa.com | Phone: +91 1242577777
          </Typography>
        </Paper>
      </Container>

      {/* ===== STANDARD FOOTER ===== */}
      <Container maxWidth="lg" sx={{ pb: 6, mt: 4 }}>
        <Paper elevation={0} sx={{ p: 3, textAlign: "center", borderRadius: 3 }}>
          <Typography variant="body2" color="text.secondary">
            For questions or clarifications regarding BankMudraa services,
            please contact our support team.
            <br />
            <strong>BankMudraa</strong> reserves the right to update platform policies without prior notice.
          </Typography>

          <Divider sx={{ my: 2 }} />

          <Typography variant="caption" color="text.secondary">
            © 2025-2026 BankMudraa. All rights reserved. |
            Techmoney Solutions Private Limited
          </Typography>
        </Paper>
      </Container>

    </Box>
  );
};

export default Disclaimer;